/**
 * @param {!object} items - the items for the combobox
 * @param {!string} selectedItem = the value of the selected item
 * @returns {object} the combobox item containing the selected item
 */
const findSelectedComboboxItem = (items, selectedItem) =>
  items.find(item => item.value === selectedItem);

export default findSelectedComboboxItem;
