const getConvertedAddress = ({ components, mapping, shortNameFields }) => {
  const convertedSuggestionAddressModel = {};

  components.forEach(address => {
    for (const addressType of address.types) {
      if (mapping[addressType]) {
        let addressFieldValue;
        if (shortNameFields.includes(addressType)) {
          addressFieldValue = address.short_name;
        } else {
          addressFieldValue = address.long_name;
        }
        const key = mapping[addressType];
        convertedSuggestionAddressModel[key] = addressFieldValue;
      }
    }
  });

  if (convertedSuggestionAddressModel.line1 && convertedSuggestionAddressModel.line2) {
    convertedSuggestionAddressModel.line1 += ` ${convertedSuggestionAddressModel.line2}`;
    delete convertedSuggestionAddressModel.line2;
  }

  return convertedSuggestionAddressModel;
};

export default getConvertedAddress;
