/**
 * Field names
 *
 * These map to the `name` prop for each field of each template.
 * They also map to the defaults for `id` and `modelKey` props for each field of each template.
 */
export const ADDITIONALLOCATION_FIELD = 'additionalLocation';
export const CITY_FIELD = 'city';
export const COUNTRYCODE_FIELD = 'countryCode';
export const COUNTY_FIELD = 'county';
export const LINE1_FIELD = 'line1';
export const LINE2_FIELD = 'line2';
export const LINE3_FIELD = 'line3';
export const NEIGHBORHOOD_FIELD = 'neighborhood';
export const POINTOFREFERENCE_FIELD = 'pointOfReference';
export const POSTALCODE_FIELD = 'postalCode';
export const STATEPROVINCECODE_FIELD = 'stateProvinceCode';
export const STREETNAME_FIELD = 'streetName';
export const STREETNUMBER_FIELD = 'streetNumber';

// This array is provided as a helper to identify what fields might be present in an address form
export const FIELD_WHITELIST = [
  ADDITIONALLOCATION_FIELD,
  CITY_FIELD,
  COUNTRYCODE_FIELD,
  COUNTY_FIELD,
  LINE1_FIELD,
  LINE2_FIELD,
  LINE3_FIELD,
  NEIGHBORHOOD_FIELD,
  POINTOFREFERENCE_FIELD,
  POSTALCODE_FIELD,
  STATEPROVINCECODE_FIELD,
  STREETNAME_FIELD,
  STREETNUMBER_FIELD
];

/**
  * Common field input masking for postalCode and stateProvinceCode
  *
  * These are provided as a convenience based on common use cases found on the web.
  * If this masking is visible onFocus of the related input field then it should exactly 
  match the `placeholder` prop for that field.
  *
  * Feel free to use these if they meet your application's needs.
  */
export const COMMON_FIELD_MASKING_VALUES = {
  AU: {
    postalCode: [/\d/, /\d/, /\d/, /\d/],
    stateProvinceCode: [/\d/, /\d/, /\d/, /\d/]
  },
  BR: {
    postalCode: [/\d/, /\d/, /\d/, /\d/, /\d/, '-', /\d/, /\d/, /\d/],
    stateProvinceCode: [/[a-zA-Z]/, /[a-zA-Z]/]
  },
  CA: {
    postalCode: [/[a-zA-Z]/, /\d/, /[a-zA-Z]/, ' ', /\d/, /[a-zA-Z]/, /\d/],
    stateProvinceCode: [/[a-zA-Z]/, /[a-zA-Z]/]
  },
  CN: {
    postalCode: [/\d/, /\d/, /\d/, /\d/, /\d/, /\d/, /\d/]
  },
  ES: {
    postalCode: [/\d/, /\d/, /\d/, /\d/, /\d/]
  },
  FR: {
    postalCode: [/\d/, /\d/, /\d/, /\d/, /\d/]
  },
  IN: {
    postalCode: [/\d/, /\d/, /\d/, /\d/, /\d/, /\d/]
  },
  MX: {
    postalCode: [/\d/, /\d/, /\d/, /\d/, /\d/]
  },
  NZ: {
    postalCode: [/\d/, /\d/, /\d/, /\d/]
  },
  PL: {
    postalCode: [/\d/, /\d/, '-', /\d/, /\d/, /\d/]
  },
  US: {
    postalCode: [/\d/, /\d/, /\d/, /\d/, /\d/],
    stateProvinceCode: [/[a-zA-Z]/, /[a-zA-Z]/]
  }
};
