import { arrayOf, bool, func, instanceOf, num, oneOf, oneOfType, shape, string } from 'prop-types';

export const ADDRESS_FIELD_OVERRIDES_PROP_TYPE = shape({
  additionalHtmlProps: shape({}),
  autoComplete: string,
  comboboxOptions: shape({
    comboboxItems: arrayOf(
      shape({
        shortValue: string,
        value: string.isRequired
      })
    ).isRequired,
    onComboboxSelect: func,
    renderComboboxOption: func.isRequired,
    selectedItem: shape({ value: string.isRequired })
  }),
  defaultValue: string,
  fieldOrder: arrayOf(string),
  hasMask: bool,
  id: string,
  mask: arrayOf(oneOfType([instanceOf(RegExp), string])),
  maxLength: num,
  name: string,
  readOnly: bool,
  renderAsCombobox: bool,
  required: bool,
  shouldRenderField: bool,
  shouldShowInputMasking: bool,
  shouldSplitField: bool,
  shouldUpperCaseValue: bool,
  splitFieldWidth: oneOf(['thirty', 'forty', 'fifty', 'sixty', 'seventy']),
  value: string
});

