/**
 * @function PURE
 * @param {string} selectedMonth
 * @param {string} selectedYear
 * @param {Number} maxYearsInFuture
 * @returns {boolean}
 */
const isValidMonthYear = (selectedMonth, selectedYear, maxYearsInFuture = 20) => {
  const currentYear = Number(
    new Date()
      .getFullYear()
      .toString()
      .substr(-2)
  );
  const currentMonth = new Date().getMonth() + 1;
  const yearsToShow = currentYear + maxYearsInFuture - 1;

  if (!selectedMonth) {
    return true;
  }

  const selectedMonthNumber = Number(selectedMonth);
  const selectedYearNumber = Number(selectedYear);

  if (Number(selectedYear) === currentYear && selectedMonthNumber < currentMonth) {
    return false;
  } else if (selectedYearNumber === yearsToShow && selectedMonthNumber > currentMonth) {
    return false;
  } else if (selectedYearNumber > yearsToShow) {
    return false;
  }

  return true;
};

export default isValidMonthYear;
