const resultCache = {};

/**
 * @description Returns a promise that resolves with a string or null for a country's stateProvinceCode.
 Uses Google Address Suggest functionality.
 This is often used for countries like India (IN) and Mexico (MX).
 Use this function if your application has requirements to populate the stateProvinceCode field of the
 address form automatically based on the postalCode value.
 This function should be called only with a complete postalCode value.
 *
 * @param {String} countryCode - the two-letter countryCode for the country of the address form
 * @param {String} postalCode - the postalCode of the address form
 * @returns {Promise.<String|null>}
 */
const loadStateProvinceCodeFromPostalCode = (countryCode, postalCode) => {
  return new Promise(resolve => {
    const cacheKey = countryCode + postalCode;
    const cachedValue = resultCache[cacheKey];

    if (cachedValue) {
      resolve(cachedValue);
      return;
    }

    let geocoder;

    try {
      geocoder = new window.google.maps.Geocoder();
    } catch (e) {
      resolve(null);
      return;
    }

    geocoder.geocode(
      {
        componentRestrictions: {
          country: countryCode,
          postalCode
        }
      },
      (results, status) => {
        try {
          const result = results && results[0];

          if (
            status === window.google.maps.GeocoderStatus.OK &&
            result.address_components.length >= 2
          ) {
            const componentLength = result.address_components.length;
            const stateIndex = componentLength - 2;
            const stateComponent = result.address_components[stateIndex];
            const countryIndex = componentLength - 1;
            const countryComponent = result.address_components[countryIndex];

            if (
              stateComponent &&
              countryComponent.short_name.toUpperCase() === countryCode.toUpperCase()
            ) {
              resultCache[cacheKey] = stateComponent.long_name;
              resolve(stateComponent.long_name);
            }
          }
        } catch (e) {
          //eslint-disable-next-line
          console.warn('Unable to load the stateProvinceCode from the provided postalCode.');
        }

        resolve(null);
      }
    );
  });
};

export default loadStateProvinceCodeFromPostalCode;
