/* eslint-disable valid-jsdoc */

import { fieldDefaults, fieldNamesAndMasks, templates } from '../constants';

/**
 * @description Get template defaults and fields based on template name
 *
 * @param {!string} template - the template name
 * @param {?arrayOf(string)} fields - use only with `custom` template; the fields and defaults that should be returned
 * @returns {object} - the template object
 */
const getTemplateDefaults = (template = 'default', fields = []) => {
  if (template === 'custom') {
    try {
      if (fields && fields.length) {
        const customTemplateDefaults = { defaults: {}, fields };
        fields
          .map(field => fieldNamesAndMasks[`${field.toUpperCase()}_FIELD`])
          .forEach(field => {
            customTemplateDefaults.defaults[field] =
              fieldDefaults[`${field}Defaults`][`${field.toUpperCase()}_DEFAULTS`];
          });
        const customTemplateDefaultsDeepCopy =
          customTemplateDefaults && JSON.parse(JSON.stringify(customTemplateDefaults));
        return customTemplateDefaultsDeepCopy;
      }
    } catch {
      throw new Error(
        'You are using the custom address template but are not passing an array of supported fields.'
      );
    }
  }
  try {
    const templateDefaults = templates[`${template.toUpperCase()}_TEMPLATE`];
    const templateDefaultsDeepCopy =
      templateDefaults && JSON.parse(JSON.stringify(templateDefaults));
    return templateDefaultsDeepCopy;
  } catch {
    throw new Error('You are using an invalid template name.');
  }
};

export default getTemplateDefaults;
