import { validationRegex } from '../constants/';

const { BR_POSTALCODE_REG_EXP } = validationRegex;

/**
  * @description - Helper to know if a full, complete CEP (Brazil postalCode) has been entered.
  This is used to help with making the CEP lookup && displaying remaining address form
  fields for Brazil.
  *
  * @param {?(string|number)} value
  *
  * @returns {bool} true if a full and complete CEP has been entered, false if not
  */
const isCompleteBrazilCEP = value =>
  BR_POSTALCODE_REG_EXP.test(
    value
      .toString()
      .replace(/[\D\s]*/g, '')
      .substring(0, 8)
  );

export default isCompleteBrazilCEP;
