const loadGoogleAddressScript = apikey => {
  if (
    typeof window.google === 'undefined' &&
    document.getElementById('googleMapsAPIScript') === null
  ) {
    const googleMapsAPIScript = document.createElement('script');
    googleMapsAPIScript.id = 'googleMapsAPIScript';
    googleMapsAPIScript.async = 'true';
    /* istanbul ignore next */
    googleMapsAPIScript.src = `https://maps.googleapis.com/maps/api/js?key=${apikey}&libraries=places`;
    /* istanbul ignore next */
    document.querySelector('body').appendChild(googleMapsAPIScript);
  }
};

export default loadGoogleAddressScript;
