const getGoogleAddressSuggestionMapping = templateName => {
  let suggestionMapping = {
    administrative_area_level_1: 'stateProvinceCode',
    country: 'countryCode',
    locality: 'city',
    postal_code: 'postalCode',
    route: 'line2',
    street_number: 'line1'
  };

  switch (templateName) {
    case 'brazil':
      suggestionMapping = {
        administrative_area_level_1: 'stateProvinceCode',
        administrative_area_level_2: 'city',
        country: 'countryCode',
        postalCode: 'postalCode',
        route: 'streetName',
        street_number: 'streetNumber',
        sublocality_level_1: 'line2'
      };
      break;
    case 'hongKong':
      suggestionMapping = {
        country: 'countryCode',
        neighborhood: 'city',
        route: 'line2',
        street_number: 'line1'
      };
      break;
    case 'mexico':
      suggestionMapping = {
        administrative_area_level_1: 'stateProvinceCode',
        locality: 'city',
        postal_code: 'postalCode',
        route: 'streetName',
        street_number: 'streetNumber',
        sublocality_level_1: 'neighborhood'
      };
      break;
    case 'noState':
      suggestionMapping = {
        country: 'countryCode',
        locality: 'city',
        postal_code: 'postalCode',
        route: 'line2',
        street_number: 'line1'
      };
      break;
    case 'streetNameNumber':
      suggestionMapping = {
        country: 'countryCode',
        locality: 'city',
        postal_code: 'postalCode',
        route: 'streetName',
        street_number: 'streetNumber'
      };
      break;
  }

  return suggestionMapping;
};

export default getGoogleAddressSuggestionMapping;
