/**
 * @description default conditional function used during list item filtering
 *
 * @param {string} listItemTextContent
 * @param {string} searchTerm
 * @return {boolean}
 */
export const filterFunction = (listItemTextContent, searchTerm) => {
  if (typeof listItemTextContent === 'string' && typeof searchTerm === 'string') {
    return listItemTextContent.toLowerCase().includes(searchTerm);
  }

  return false;
};

/**
 * @description filter as you type feature, it use searchIncludeFields to decide
 * which fields to search on, if searchIncludeFields is not defined, it will only
 * search on the itemKey field
 *
 * @param {string} inputValue
 * @param {object} optionsTemplate
 * @param {function} filterFn
 * @returns {object}
 */
export const filterOptionValues = (inputValue, optionsTemplate, filterFn = filterFunction) => {
  const { itemKey, optionValues, searchIncludeFields } = optionsTemplate;
  let count = 0;
  //if user do not specify searchFields, then will only search on itemKey.
  const searchFields = searchIncludeFields ? searchIncludeFields : [itemKey];
  let itemValues;
  if (optionValues && optionValues.length > 0) {
    itemValues = optionValues.filter(optionObj => {
      for (const key of searchFields) {
        if (optionObj[key]) {
          if (filterFn(optionObj[key], inputValue)) {
            count++;
            return true;
          }
        }
      }
      return false;
    });
  }
  return { count, itemValues };
};
