/**
 * @function PURE
 * @description get the list of year
 * @param {number} maxYearsInFuture
 * @returns {array} Year list
 */
const getYears = (maxYearsInFuture = 20) => {
  let currentYear = Number(
    new Date()
      .getFullYear()
      .toString()
      .substr(-2)
  );
  const yearsToShow = currentYear + maxYearsInFuture - 1;
  const years = [];

  while (currentYear <= yearsToShow) {
    years.push(`${currentYear}`);
    currentYear++;
  }

  return years;
};

export default getYears;
