/* CA RegExp */
/**
 * CA postalCode follows pattern alpha - num - alpha - (? space) - num - alpha - num
 * Example CA postalCode (with a space): A1A 1A1
 * Example CA postalCode (no space): B2B2B2
 */
export const CA_POSTALCODE_REG_EXP = /^\s*([ABCEFGHJKLMNPRSTVXY][0-9][ABCEFGHJKLMNPRSTVWXYZ][\s]?[0-9][ABCEFGHJKLMNPRSTVWXYZ][0-9])\s*$/i;
/* use CA_STATEPROVINCECODE for stateProvinceCode field if your application uses an input field requiring two-letter code for stateProvinceCode */
export const CA_STATEPROVINCECODE_REG_EXP = /^(?:AB|BC|MB|NB|NL|NS|NT|NU|ON|PE|QC|SK|YT)$/i;

/*******************************************************/
